<template>
  <div>
    <div class="progress_bar">
      <div class="background-bar">
        <transition appear @before-appear="beforeEnter" @after-appear="enter">
          <div data-aos="new-animation" style="height:28px;border-radius: 20px;">
            <div class="info">
              <label>{{ label }}</label>
              <label class="percentage">{{ percentage }}% </label>
            </div>
          </div>
        </transition>
      </div>
    </div>
    
  </div>
</template>
<script>
export default {
  props: {
    percentage: Number,
    label: String
  },
  methods: {
    beforeEnter(el) {
      el.style.width = 0;
    },
    enter(el) {
      el.style.width = `${this.percentage}%`;
      el.style.transition = `width 1s linear`;
    }
  }
};
</script>
<style scoped>
.progress_bar {
  width: 100%;
  margin-bottom: 20px;
 
}
.progress_bar label {
  color: white;
   padding-left: 10px;
}
.progress_bar .info {
  font-size: 17px;
  -webkit-box-pack: justify;
  justify-content: space-between;
  display: -webkit-box;
  display: flex;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  z-index: 1;
    height: 37px;
  align-items: center;
}
.progress_bar .info .percentage {
  font-weight: bolder;
}
.progress_bar .background-bar {
  background: lightgray;
  width: 100%;
  height: 28px;
    border-radius: 20px;
}
.progress_bar .tracker-bar {
 
}
[data-aos="new-animation"]{
   background: #537895;
  height: 30px;
  width: 0;
  -webkit-transition: width 0.5s linear;
 
  margin-top: -5px;
 transition: width 0.5s linear;
}
</style>
